import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, DatePicker, Input } from 'antd';
import { createDormRoomStudents } from 'services/inOutStudent';
import { dateToString } from 'utils/formatUtil';

import { requiredRule } from 'utils/validateRules';
import { SelectAjax } from 'components/common';
import {
  getDormitorys,
  getDormitoryFloors,
  getDormitoryRooms,
} from 'services/dormitoryForInOutStudent';
import { getQueryParams } from 'utils/urlUtil';

const { TextArea } = Input;
@Form.create({ name: 'formInStudent' })
class FormInStudent extends Component {
  state = {
    studentId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ studentId: queryParamId });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
      console.log(values);
    });
  };

  saveData = async values => {
    const { studentId } = this.state;
    const dateValue = {
      ...values,
      'date-picker': values['date-picker'].format('YYYY-MM-DD'),
    };
    try {
      await createDormRoomStudents({
        studentId,
        dormRoomId: values.dormRoomId,
        startDate: dateToString(dateValue['date-picker'], 'YYYY/MM/DD'),
        note: values.note,
      });
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  handleChange = value => {
    console.log(`selected ${value}`);
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const dormId = getFieldValue('dormId');
    const floorId = getFieldValue('floorId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const config = {
      rules: [{ type: 'object', message: 'Please select time!' }],
    };
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Từ ngày">
              {getFieldDecorator('date-picker', config)(<DatePicker />)}
            </Form.Item>
            <Form.Item label="Tòa nhà">
              {getFieldDecorator('dormId', {
                initialValue: initialValue.dormId,
                rules: [requiredRule],
              })(<SelectAjax service={getDormitorys} />)}
            </Form.Item>
            <Form.Item label="Tầng">
              {getFieldDecorator('floorId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  params={{ dormId: getFieldValue('dormId') }}
                  service={getDormitoryFloors}
                  allowGetData={!!dormId}
                />
              )}
            </Form.Item>
            <Form.Item label="Số phòng">
              {getFieldDecorator('dormRoomId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  params={{ dormId: getFieldValue('dormId'), floorId: getFieldValue('floorId') }}
                  service={getDormitoryRooms}
                  allowGetData={!!floorId}
                  keyLabel="roomNo"
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ghi chú" hasFeedback>
              {getFieldDecorator('note', {
                initialValue: initialValue.note,
                rules: [
                  {
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea rows={8} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormInStudent {...props} />
  </Page>
));



